(function(){
  let projets = [],
    sections = [],
    ul = $('#portfolio ul'),
    h4 = {}

  let req = new XMLHttpRequest();
  req.open("GET", "projets.json", true);
  req.onreadystatechange = processJson;
  req.send(null);


  function processJson(){
    if (req.readyState == 4){
      let doc = eval('(' + req.responseText + ')')
      doc = Object.entries(doc)

      doc.map((arr) => {

        let section = arr[0].replace(arr[0][0], arr[0][0].toUpperCase())
        h4[section] = $('<h4 id="'+section+'">')
        ul.append($('<div class="row">').append(h4[section].text(section)))

        arr[1].map(parsingArr)

      })
    }

  }


  function parsingArr(obj, i, arr){

    if(Object.keys(obj).length>0){

      let li = $('<li class="col-xs-12 col-sm-6 col-md-3">'),
      img = $('<figure><img class="img-portfolio img-responsive img-thumbnail" src="'+obj.thumbnail+'"></figure>'),
        a = $('<a href="'+obj.link+'" target="_blank">'),
        portfolioItem = $('<div class="portfolio-item">'),
        figcaption = $('<figcaption>'),
        keywords = obj.keywords,
        p = $('<p>').html("<h4>"+obj.title+"</h4>"+obj.description)

      if(keywords){
        keywords = keywords.map((keyword) => {
          return '<strong>'+keyword+'</strong>'
        })

        figcaption.html(keywords.join(' - '))
      }

      let value
      for (value in h4){
        h4[value].after(
          li.append(
            portfolioItem.append(
              a.append(img.append(figcaption),
                      p)
            )
          )
        )


      }

    }


  }

})();





